import { useEffect, useState } from "react"

import Footer from "../components/footer"
import Header from "../components/header"
import Hotline from "../components/hotline"
import { userOrder } from "../api/userApi"
import { useNavigate } from "react-router-dom"

import wishlist from '../img/wishlist.png'

const Order = () => {
    const navigate = useNavigate()
    const [order, setOrder] = useState([])

    useEffect(() => {
        (async () => {
            const token = localStorage.getItem('user')
            const res = await userOrder(token)
            setOrder(res)
        })()
    }, [])

    return (
        <>
            <Header />
            {
                order && order.length
                ?
                <div className="my-8 mx-20 flex flex-col items-center">
                    <p className="font-primary text-primary_color text-2xl font-bold">N HNG</p>
                    <div className="overflow-y-auto h-160 flex flex-col gap-4 gap-y-6 mt-8">
                        {
                            order && order.map((item)=>(
                                <div key={item.orderID} className="lg:w-[500px] w-[450px] flex flex-row justify-between border-b-2 border-gray-100 pt-8">
                                    <div className="flex flex-col flex-wrap gap-4">
                                        <p><b>M n hng:</b> {item.orderID}</p>
                                        <p><b>Phng thc thanh ton:</b> {item.paymentMethod}</p>
                                    </div>

                                    <div className="flex flex-col flex-wrap gap-4">
                                        <p><b>S tin:</b> {item.totalPrice} VND</p>
                                        <p><b>Tnh trng:</b> {(item.isProcessed === 0)?"ang x l":" hon thnh"}</p>
                                    </div>
                                </div>
                            ))
                        }
                    </div>
                </div>
                :
                <div className="flex flex-col text-center justify-center items-center gap-y-8 pt-20">
                        <p className="font-primary text-primary_color text-3xl text-bold">n hng ca bn</p>
                        <p>Hin bn ang cha c n hng no</p>
                        <img className="ml-8 max-w-[200px]" src={wishlist} alt="list"/>
                        <p>Xem qua cc mt hng v mua ngay no!</p>
                        <button className="text-white font-semibold bg-primary_color w-fit rounded-xl p-2 px-6" onClick={()=>{navigate('/pet')}}>Mua hng ngay</button>
                </div>
            }

            <Hotline />
            <Footer />
        </>
    )
}

export default Order;